import React, { useContext, useEffect } from "react";
import { MyContext } from "./Context";
const { kakao } = window;

const Map = () => {
  const { latLon, targetLoc, setTargetLoc, mainCont, setMainCont } = useContext(MyContext);

  useEffect(() => {
    let iwContent = `<div style="width:150px;text-align:center;padding:6px 0;">${targetLoc.name}</div>`;
    let infowindow = new kakao.maps.InfoWindow({
      content: iwContent,
    });
    let placeOverlay = new kakao.maps.CustomOverlay({ zIndex: 1 }),
      contentNode = document.createElement("div"), //     
      markers = [], //   
      currCategory = ""; //      

    let mapContainer = document.getElementById("map"), //   div
      mapOption = {
        center: new kakao.maps.LatLng(37.56634, 126.97945), //  
        level: 2, //   
        maxLevel: 5,
      };

    //  
    let map = new kakao.maps.Map(mapContainer, mapOption);
    let myLoc = new kakao.maps.LatLng(latLon[0], latLon[1]);
    let target = new kakao.maps.LatLng(targetLoc.lat, targetLoc.lng);

    //    
    if (targetLoc?.name !== " ") {
      map.panTo(target);
      displayMarker(target);
    } else if (latLon[0] !== undefined) {
      map.panTo(myLoc);
      displayMarker2(myLoc);
    }
    // btn-loc    
    const btnLoc = document.getElementById("loc-btn");
    btnLoc.addEventListener("click", () => {
      setTargetLoc({ name: " ", lat: latLon[0], lng: latLon[1] });
      //    
      map.panTo(myLoc);
      displayMarker2(myLoc);
    });

    function displayMarker(locPosition) {
      //  
      let marker = new kakao.maps.Marker({
        map: map,
        position: locPosition,
      });
      infowindow.open(map, marker);
    }

    function displayMarker2(locPosition) {
      //  
      let marker = new kakao.maps.Marker({
        map: map,
        position: locPosition,
      });
      infowindow.close(map, marker);
    }
    //    
    let ps = new kakao.maps.services.Places(map);

    //  idle  
    kakao.maps.event.addListener(map, "idle", searchPlaces);

    //     css class 
    contentNode.className = "placeinfo_wrap";

    //     mousedown, touchstart  
    //        kakao.maps.event.preventMap  
    addEventHandle(contentNode, "mousedown", kakao.maps.event.preventMap);
    addEventHandle(contentNode, "touchstart", kakao.maps.event.preventMap);

    //    
    placeOverlay.setContent(contentNode);

    //     
    addCategoryClickEvent();

    //     
    function addEventHandle(target, type, callback) {
      if (target.addEventListener) {
        target.addEventListener(type, callback);
      } else {
        target.attachEvent("on" + type, callback);
      }
    }

    //    
    function searchPlaces() {
      if (!currCategory) {
        return;
      }

      //   
      placeOverlay.setMap(null);

      //     
      removeMarker();

      ps.categorySearch(currCategory, placesSearchCB, { useMapBounds: true });
    }

    //      
    function placesSearchCB(data, status, pagination) {
      if (status === kakao.maps.services.Status.OK) {
        //      
        displayPlaces(data);
      } else if (status === kakao.maps.services.Status.ZERO_RESULT) {
        //        
      } else if (status === kakao.maps.services.Status.ERROR) {
        //            e
      }
    }

    //    
    function displayPlaces(places) {
      //     
      //       
      let order = document.getElementById(currCategory).getAttribute("data-order");

      for (let i = 0; i < places.length; i++) {
        //    
        let marker = addMarker(new kakao.maps.LatLng(places[i].y, places[i].x), order);

        //      
        //     
        (function (marker, place) {
          kakao.maps.event.addListener(marker, "click", function () {
            //     
            map.panTo(new kakao.maps.LatLng(place.y, place.x));
            setTimeout(() => {
              displayPlaceInfo(place);
            }, 350);
          });
        })(marker, places[i]);
      }
    }

    //       
    function addMarker(position, order) {
      let imageSrc = process.env.PUBLIC_URL + "/assets/images/spritesheet2.jpg", //   url,   
        imageSize = new kakao.maps.Size(27, 28), //   
        imgOptions = {
          spriteSize: new kakao.maps.Size(162, 27), //   
          spriteOrigin: new kakao.maps.Point(order * 27, 0, 0, 0), //       
          offset: new kakao.maps.Point(11, 28), //      
        },
        markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imgOptions),
        marker = new kakao.maps.Marker({
          position: position, //  
          image: markerImage,
        });

      marker.setMap(map); //    
      markers.push(marker); //    

      return marker;
    }

    //       
    function removeMarker() {
      for (let i = 0; i < markers.length; i++) {
        markers[i].setMap(null);
      }
      markers = [];
    }

    //         
    function displayPlaceInfo(place) {
      let content = `<div class="placeinfo">   <a class="title" href="${place.place_url}" target="_blank" title="${place.place_name}">${place.place_name}</a>`;

      if (place.road_address_name) {
        content +=
          '    <span title="' +
          place.road_address_name +
          '">' +
          place.road_address_name +
          "</span>" +
          '  <span class="jibun" title="' +
          place.address_name +
          '">( : ' +
          place.address_name +
          ")</span>";
      } else {
        content += '    <span title="' + place.address_name + '">' + place.address_name + "</span>";
      }

      content += `<span class="tel">${place.phone}<div class="after"></div>`;

      contentNode.innerHTML = content;
      placeOverlay.setPosition(new kakao.maps.LatLng(place.y, place.x));
      placeOverlay.setMap(map);
    }

    //     
    function addCategoryClickEvent() {
      let category = document.getElementById("category"),
        children = category.children;

      for (let i = 0; i < children.length; i++) {
        children[i].onclick = onClickCategory;
      }
    }

    //     
    function onClickCategory() {
      let id = this.id,
        className = this.className;
      placeOverlay.setMap(null);

      if (className === "on") {
        currCategory = "";
        changeCategoryClass();
        removeMarker();
      } else {
        currCategory = id;
        changeCategoryClass(this);
        searchPlaces();
        console.log(currCategory);
      }
    }
    //      
    function changeCategoryClass(el) {
      let category = document.getElementById("category"),
        children = category.children,
        i;

      for (i = 0; i < children.length; i++) {
        children[i].className = "";
      }

      if (el) {
        el.className = "on";
      }
    }
  }, [latLon]);

  return (
    <div className='map_wrap'>
      <div id='map'></div>
      <ul id='category'>
        <li id='BK9' data-order='0'>
          <span className='category_bg bank'></span>
          
        </li>
        <li id='PK6' data-order='1'>
          <span className='category_bg car-park'></span>
          
        </li>
        <li id='SW8' data-order='2'>
          <span className='category_bg train'></span>
          
        </li>
        <li id='FD6' data-order='3'>
          <span className='category_bg restaurant'></span>
          
        </li>
        <li id='CE7' data-order='4'>
          <span className='category_bg cafe'></span>
          
        </li>
        <li id='CS2' data-order='5'>
          <span className='category_bg store'></span>
          
        </li>
      </ul>
      <div id='loc-btn'>
        <button id='btn-loc' />
      </div>
    </div>
  );
};

export default Map;
